<script>
import { mapGetters, mapActions } from "vuex";
// import { resetRouter } from "@/router/index.js"; //importexport function
export default {
    name: "index",
    data() {
        return {
            active: false,
            slide: 0,
            sliding: null,
        };
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            loginData: "getlogin/get_loginData",
            pageAccess: "getlogin/get_pageAccess",
            pageList: "getlogin/get_pageList",
            DEFAULT_pageList: "getlogin/get_DEFAULT_pageList",
            DEFAULT_commonModalConfig: "usemodal/get_DEFAULT_commonModalConfig",
            // system: "apidetail/get_system",
        }),
    },
    created: function () {
        console.log(this.$router.currentRoute.path);
        // this._handlePageAccess();
        console.log(this.pageAccess);
        console.log(this.$router);
        console.log(this.$router.currentRoute.matched);
        // console.log(this.system);
        console.log(this.$route.params.SYSTEM);
    },
    methods: {
        ...mapActions({
            changeLoginData: "getlogin/change_loginData",
            setPageList: "getlogin/set_pageList",
        }),

        _onSlideStart(slide) {
            this.sliding = true;
        },
        _onSlideEnd(slide) {
            this.sliding = false;
        },

        _handlePageAccess() {
            let vm = this;
            console.log(vm.pageAccess);
            console.log(vm.DEFAULT_pageList);
            let this_pagelist = JSON.parse(JSON.stringify(vm.DEFAULT_pageList));
            console.log(this_pagelist);
            Object.entries(vm.pageAccess).map((element) => {
                if (!element[1]["authority"]) {
                    let thisindex = this_pagelist["name"].indexOf(element[0]);
                    this_pagelist["name"].splice(thisindex, 1);
                    this_pagelist["trans"].splice(thisindex, 1);
                }
            });
            console.log(this_pagelist);
            vm.setPageList(this_pagelist);
        },

        _checkRouteIsActive: function (item) {
            console.log(item);
            console.log(this.$router.currentRoute.path);
            const pattern = new RegExp("(/" + item + ")", "g");
            return {
                isactive: pattern.test(this.$router.currentRoute.path),
            };
        },

        _goToPosition(value) {
            // this.$nextTick(() => {
            //
            this.$el.querySelector("#" + value).scrollIntoView({
                behavior: "smooth", // 
                block: "start", // 
            });
            // });
        },

        _logout() {
            var vm = this;
            var obj = {};
            obj.account = null;
            obj.status = false;
            obj.username = null;
            obj.accesslist = null;
            vm.changeLoginData(obj);
            // resetRouter();
            vm.$router.push("/");
        },
    },
};
</script>

